import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
    // data
    state: {
        // 全局账户体系面板状态
        loginDivStatus: false,
        registerDiv: false,
        forgetDiv: false,

        loginStatus: false,

        // 账户信息
        userInfo: {
            // 基本信息
            a_id: '',
            a_name: '',
            a_email: '',
            a_auth: '',

            // Token
            access_token: '',
            refresh_token: '',

            // 每次更新Token时自动刷新时间
            access_expires: '',
            refresh_expires: '',
        }
    },
    // computed
    getters: {

    },
    // methods 同步方法 -> commit
    mutations: {
        // 账户状态设置
        openLoginDiv(state){
            state.loginDivStatus = true;
        },
        closeLoginDiv(state) {
            state.loginDivStatus = false;
        },
        openRegisterDiv(state) {
            state.registerDiv = true;
        },
        closeRegisterDiv(state) {
            state.registerDiv = false;
        },
        openForgetDiv(state) {
            state.forgetDiv = true;
        },
        closeForgetDiv(state) {
            state.forgetDiv = false;
        },

        // 全局账户信息
        setUserInfo(state, data) {
            for (var key in data) {
                if (data.hasOwnProperty(key) === true) {
                    state.userInfo[key] = data[key];
                }
            }
            state.userInfo.access_expires = Math.round(new Date() / 1000) + 3600;
            state.userInfo.refresh_expires = Math.round(new Date() / 1000) + 604800;
        },

        // 登录状态
        changeLoginStatus(state, value){
            state.loginStatus = value;
        },
    },
    // methods 异步方法 -> dispatch
    actions: {

    }
})


